.explanation {
  &__wrap {
    background: #EEFAFF;
    padding: 60px 50px;
    display: flex;
    flex-wrap: wrap; }
  &__first {
    max-width: 584px;
    margin-right: 20px;
    &-title {
      font-size: 32px; }
    &-text {
      font-size: 16px;
      font-weight: 300;
      margin-top: 20px;
      b {
        font-weight: 500; } } }
  &__second {
    width: 401px;
    margin-left: auto;
    background: linear-gradient(182.31deg, #64D8FF 1.82%, #33C6F8 100.25%);
    border-radius: 10px;
    padding: 30px 40px;
    color: #024962;
    &-title {
      font-size: 22px;
      margin-bottom: 15px; }
    &-text {
      font-size: 16px;
      font-weight: 300; } }
  &__bottom {
    margin-top: 40px;
    &-title {
      font-size: 22px; }
    &-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: 30px; }
    &-item {
      width: 50%;
      padding-right: 30px;
      margin-bottom: 25px;
      &-title {
        font-weight: 600;
        font-size: 18px;
        margin-top: 15px; }
      &-text {
        font-weight: 300;
        font-size: 16px;
        margin-top: 15px;
        max-width: 400px; } } } }

@media (max-width: 1121px) {
  .explanation {
    &__first {
      max-width: none;
      margin-right: 0; }
    &__second {
      width: auto;
      margin: auto;
      margin-top: 40px; } } }

@media (max-width: 900px) {
  .explanation {
    margin: 0;
    &__wrap {
      padding: 30px 15px; }
    &__first {
      &-title {
        font-size: 24px; }
      &-text {
        font-size: 14px; } }
    &__second {
      margin-top: 20px;
      padding: 20px 25px;
      &-title {
        font-size: 18px;
        margin-bottom: 10px; }
      &-text {
        font-size: 12px; } }
    &__bottom {
      margin-top: 30px;
      &-title {
        font-size: 18px; }
      &-wrap {
        display: flex;
        align-items: center; }
      &-item {
        width: auto;
        padding-right: 0;
        margin-bottom: 20px;
        &-title {
          font-size: 16px;
          margin-top: 0;
          margin-left: 15px; }
        &-text {
          font-size: 14px;
          max-width: none; } } } } }
