.header {
  padding-top: 263px;
  padding-bottom: 197px;
  display: flex;
  overflow: hidden;
  &__wrap {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      right: -320px;
      width: 954px;
      height: 926px;
      background: #EEFAFF;
      border-radius: 132px;
      transform: rotate(-15deg);
      animation: pulsing 10s linear infinite; } }
  &__div {
    position: relative; }
  &__title {
    font-size: 42px;
    margin-bottom: 20px;
    max-width: 520px; }
  &__text {
    font-weight: 300;
    font-size: 20px;
    line-height: 130.29%;
    margin-bottom: 40px;
    max-width: 569px; }
  &__img {
    position: absolute;
    top: calc(50% - 285px);
    right: -160px;
    width: 774px; } }

@media (max-width: 1200px) {
  .header {
    &__wrap {
      width: 100%;
      margin: 0 30px; } } }

@keyframes pulsing {
  0% {
    transform: scale(1) rotate(-15deg); }
  50% {
    transform: scale(1.1) rotate(-15deg); }
  100% {
    transform: scale(1) rotate(-15deg); } }


@media (max-width: 1100px) {
  .header {
    padding-top: 170px;
    padding-bottom: 520px;
    &__wrap::before {
        bottom: auto;
        top: 70%;
        right: 50%;
        transform: translateX(50%) rotate(-15deg);
        width: 550px;
        height: 550px; }
    &__title,
    &__text {
      max-width: none; }
    &__img {
      top: calc(100% - 40px);
      right: 50%;
      transform: translateX(50%); } }

  @keyframes pulsing {
    0% {
      transform: scale(1) rotate(-15deg) translateX(50%); }
    50% {
      transform: scale(1.1) rotate(-15deg) translateX(50%); }
    100% {
      transform: scale(1) rotate(-15deg) translateX(50%); } } }

@media (max-width: 900px) {
  .header {
    padding-bottom: 355px;
    padding-top: 106px;
    &__wrap {
      margin: 0 15px; }
    &__title {
      font-size: 24px; }
    &__text {
      font-size: 14px;
      margin-bottom: 20px; }
    &__img {
      width: 310px;
      top: calc(100% + 30px); }
    &__wrap::before {
        top: 90%;
        width: 350px;
        height: 350px;
        border-radius: 72px; } } }
