.distinction {
  &__wrap {
    background: #EEFAFF;
    border-radius: 10px;
    padding: 70px 47px;
    padding-left: 70px; }
  &__title {
    font-size: 32px; }
  &__text {
    font-weight: 300;
    font-size: 18px;
    max-width: 790px;
    margin-top: 20px; }
  &__list {
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  &__item {
    width: 340px;
    margin-top: 25px;
    padding-right: 20px;
    &-title {
      font-weight: 600;
      font-size: 18px;
      margin-top: 20px; }
    &-text {
      font-weight: 300;
      font-size: 16px;
      margin-top: 10px; } } }

@media(max-width: 1213px) {
  .distinction {
    &__item {
      width: 49%; } } }

@media(max-width: 900px) {
  .distinction {
    margin: 0;
    &__wrap {
      padding: 50px 15px; }
    &__title {
      font-size: 24px; }
    &__text {
      font-size: 16px;
      margin-top: 10px; }
    &__list {
      margin-top: 10px; }
    &__item {
      margin-top: 20px;
      &-block {
        display: flex;
        align-items: center; }
      &-img {
        width: 39px; }
      &-title {
        margin: 0;
        margin-left: 15px;
        font-size: 16px; }
      &-text {
        font-size: 14px; } } } }

@media(max-width: 550px) {
  .distinction {
    &__item {
      width: 100%;
      padding: 0; } } }
