.calculation {
  height: 1046px;
  background: linear-gradient(3deg, #fff 10%, #EEFAFF 10% 90%, #fff 90%);
  &__wrap {
    padding-top: 127px; }
  &__title {
    font-size: 32px; }
  &__text {
    font-weight: 300;
    font-size: 18px;
    max-width: 560px;
    margin-top: 20px; }
  &__wrapper {
    display: flex;
    margin-top: 40px; }
  &__form,
  &__block {
    background: #FFFFFF;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    padding: 40px 50px; }
  &__form {
    flex-grow: 2;
    margin-right: 20px;
    max-width: 657px; }
  &__label {
    font-family: Gilroy, sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: #362626;
    margin-bottom: 15px; }
  &__input {
    margin-bottom: 35px;
    height: 55px;
    padding-left: 30px;
    font-family: Gotham Pro, sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 145%;
    color: #606263;
    border: 1px solid #C4C0C0;
    background: #fff;
    border-radius: 5px;
    transition: .2s;
    &:last-child {
      margin-bottom: 0; }
    &:hover,
    &:focus {
      background: #EEFAFF;
      border: 1px solid #33C6F8; } }
  &__block {
    max-width: 463px;
    &-title {
      font-weight: 600;
      font-size: 18px;
      margin-bottom: 17px; }
    &-text {
      font-weight: 400;
      font-size: 18px;
      margin-bottom: 17px; }
    &-span {
      font-family: Gilroy;
      font-weight: bold;
      font-size: 32px;
      line-height: 39px;
      color: #33C6F8; }
    & .button {
      margin-top: auto; } } }

@media(max-width: 1000px) {
  .calculation {
    &__wrapper {
      flex-direction: column; }
    &__form {
      margin-right: 0; }
    &__block {
      margin-top: 10px; } } }

@media(max-width: 900px) {
  .calculation {
    &__title {
      font-size: 24px; }
    &__text {
      font-size: 14px; }
    &__wrapper {
      margin-top: 20px; }
    &__form,
    &__block {
      padding: 30px 20px; }
    &__label {
      font-size: 16px;
      margin-bottom: 10px; }
    &__input {
      font-size: 14px;
      padding-left: 20px;
      margin-bottom: 20px; }
    &__block .button {
      min-width: 250px;
      font-size: 12px;
      height: 56px;
      margin-top: 10px; }
    &__block {
      &-title {
        font-size: 16px;
        margin-bottom: 10px; }
      &-text {
        margin-bottom: 10px;
        font-size: 14px; }
      &-span {
        font-size: 24px; } } } }
