.button {
  position: relative;
  border: none;
  background: #26B58B;
  border-radius: 5px;
  min-width: 334px;
  height: 60px;
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
  line-height: 130.29%;
  color: #fff;
  transform-origin: 0 50%;
  z-index: 10;
  &:hover,
  &:focus {
    transform: scale(1.1); }
  &:active {
    transform: scale(1); } }

@media (max-width: 900px) {
  .button {
    font-size: 14px;
    &:hover,
    &:focus {
      transform: scale(1); } } }

@media (max-width: 550px) {
  .button {
    min-width: auto;
    width: 100%; } }
