.adviser {
  &__wrap {
    margin-bottom: 100px; }
  &__top {
    text-align: center;
    max-width: 759px;
    margin: auto;
    &-title {
      font-size: 32px;
      span {
        color: #66BEEB; } }
    &-text {
      font-size: 18px;
      font-weight: 300;
      margin-top: 25px;
      b {
        font-weight: 500; } } }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 40px;
    &-title {
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 12px; }
    &-text {
      font-size: 16px;
      font-weight: 300;
      max-width: 336px;
      margin-bottom: 32px;
      &:last-child {
        margin-bottom: 0; } } }
  &__img {
    margin-left: -170px; }
  &__block {
    max-width: 580px;
    &-text {
      padding-left: 80px;
      margin-bottom: 40px;
      &--img1 {
        background: url(../img/adviser/img-1.png) no-repeat left 3px; }
      &--img2 {
        background: url(../img/adviser/img-2.png) no-repeat left 3px; }
      &-title {
        font-size: 22px; }
      &-text {
        font-size: 16px;
        font-weight: 300;
        margin-top: 10px; } } } }

@media(max-width: 1500px) {
  .adviser {
    &__img {
      width: 50%;
      margin: 0;
      margin-right: 20px; } } }

@media(max-width: 900px) {
  .adviser {
    &__wrap {
      margin-bottom: 40px; }
    &__top {
      max-width: none;
      &-title {
        font-size: 24px; }
      &-text {
        font-size: 14px;
        margin-top: 15px; } }
    &__img {
      width: 100%;
      max-width: 450px;
      margin: auto; }
    &__bottom {
      flex-direction: column;
      &-title {
        font-size: 16px;
        margin-bottom: 8px; }
      &-text {
        font-size: 14px;
        margin-bottom: 14px;
        &:last-child {
          margin-bottom: 0; } } }
    &__block {
      margin-top: 30px;
      &-text {
        margin-bottom: 20px;
        background-size: 32px;
        background-position: 0 0;
        padding-left: 50px;
        &-title {
          padding-top: 5px;
          font-size: 18px; }
        &-text {
          font-size: 14px;
          margin-left: -50px; } } } } }
