.nav {
  max-width: 1715px;
  width: 100%;
  z-index: 20;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  &__wrap {
    margin: 0 15px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
  &__logo {
    width: 213px; }
  &__list {
    display: flex;
    margin-bottom: 16px;
    margin-right: auto;
    margin-left: 67px; }
  &__item {
    margin-right: 30px;
    &:last-child {
      margin-right: 0; } }
  &__link {
    color: #252829;
    text-decoration: none;
    font-weight: 300;
    font-size: 16px;
    transition: .2s;
    outline: none;
    &:hover,
    &:focus {
      font-weight: 500;
      color: #33C6F8; }
    &:active {
      font-weight: 300; } }
  &__right-block {
    display: flex;
    align-items: center; }
  &__button {
    display: none; }
  &__btn {
    border: none;
    background: transparent;
    font-weight: 500;
    font-size: 16px;
    cursor: pointer;
    transform-origin: 100% 50%;
    &--registration {
      color: #33C6F8;
      margin-right: 28px;
      &:hover,
      &:focus {
        color: #34BEED;
        transform: scale(1.1); }
      &:active {
        transform: scale(1); } }
    &--login {
      color: #fff;
      background: #33C6F8;
      border-radius: 5px;
      width: 147px;
      height: 52px;
      &:hover,
      &:focus {
        background: linear-gradient(302.27deg, #64D8FF 20.95%, #33C6F8 75.63%);
        transform: scale(1.1); }
      &:active {
        transform: scale(1); } } } }

@media(max-width: 1600px) {
  .nav {
    &__list {
      margin-left: 30px; }
    &__item:first-child {
      display: none; } } }

@media (max-width: 1500px) {
  .nav {
    &__item:nth-child(5) {
      display: none; } } }

@media (max-width: 1250px) {
  .nav {
    &__logo {
      width: 190px; }
    &__item:nth-child(2) {
      display: none; } } }

@media (max-width: 1100px) {
  .nav {
    &__item:nth-child(6) {
      display: none; } } }

@media(max-width: 900px) {
  .nav {
    &__button {
      display: block;
      position: fixed;
      top: 20px;
      right: 15px;
      z-index: 100;
      cursor: pointer;
      transition: .2s;
      width: 46px;
      height: 46px;
      background: #EEFAFF;
      border: none;
      border-radius: 50%;
      box-shadow: 0 2px 10px rgba(#000, .2);
      span,
      span::before,
      span::after {
        position: absolute;
        top: 50%;
        left: calc(50% - 10px);
        display: block;
        width: 19px;
        height: 1px;
        background: #33C6F8;
        transition: .2s; }
      span::before,
      span::after {
        content: '';
        top: 0;
        left: 0;
        width: 15px;
        transform-origin: 0% 50%; }
      span::before {
        transform: translateY(6px); }
      span::after {
        transform: translateY(-6px); }
      &--active {
        box-shadow: 0 2px 10px rgba(#000, 0);
        background: #fff;
        span {
          background: #fff; }
        span::before,
        span::after {
          width: 17px;
          left: 4px; }
        span::before {
          transform: translateY(6px) rotate(-45deg); }
        span::after {
          transform: translateY(-6px) rotate(45deg); } } }
    &__logo {
      width: 138px; }
    &__list,
    &__right-block {
      display: none; }
    &--active {
      position: fixed;
      top: 0;
      background: #EEFAFF;
      padding: 20px 0 30px 0;
      box-shadow: 0 2px 10px rgba(#000, .1);
      & .nav {
        &__wrap {
          flex-direction: column;
          align-items: flex-start; }
        &__list,
        &__right-block {
          display: flex;
          flex-direction: column;
          margin: 0; }
        &__item {
          display: block;
          margin: 0;
          margin-top: 30px; }
        &__right-block {
          margin-top: 80px; }
        &__btn {
          transform-origin: 0% 50%;
          &--registration {
            margin-bottom: 20px; } } } } } }

