.blurb {
  &__wrap {
    background: linear-gradient(320.89deg, #64D8FF 20.95%, #33C6F8 75.63%);
    border-radius: 100px 10px;
    padding: 60px 80px;
    margin-top: 110px;
    margin-bottom: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__title {
    font-size: 32px;
    color: #fff;
    max-width: 601px;
    margin-right: 20px; } }

@media (max-width: 1150px) {
  .blurb {
    &__wrap {
      flex-wrap: wrap; }
    &__title {
      margin-right: 0;
      margin-bottom: 20px; } } }

@media (max-width: 900px) {
  .blurb {
    &__wrap {
      margin-top: 50px;
      margin-bottom: 40px;
      padding: 60px 30px; }
    &__title {
      font-size: 24px;
      max-width: none; }
    & .button {
        font-size: 12px; } } }

@media (max-width: 550px) {
  .blurb {
    &__wrap {
      padding: 60px 20px; } } }
