.stages {
  &__wrap {
    padding-bottom: 50px; }
  &__title {
    font-size: 32px;
    text-align: center; }
  &__text {
    font-weight: 300;
    font-size: 18px;
    text-align: center;
    max-width: 537px;
    margin: 0 auto;
    margin-top: 20px;
    b {
      font-weight: 500; } }
  &__list {
    max-width: 934px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top: 40px; }
  &__item {
    width: 100%;
    display: flex;
    justify-content: center;
    padding-bottom: 70px;
    position: relative;
    &:last-child {
      padding-bottom: 0; }
    &::before {
      content: '';
      top: 0;
      left: 50%;
      position: absolute;
      width: 1px;
      height: 100%;
      border-left: 1px dashed #A7E9FF; }
    & .button {
      margin-top: 30px; }
    &-img {
      width: 150px;
      margin-left: auto;
      margin-right: 30px;
      margin-bottom: auto;
      &-2 {
        width: 239px;
        margin-bottom: auto; } }
    &-block-1,
    &-block-3 {
      width: 40%;
      display: flex;
      flex-direction: column;
      justify-content: center; }
    &-block-2 {
      width: 20%; }
    &-title {
      font-weight: 600;
      font-size: 18px; }
    &-text {
      font-weight: 300;
      font-size: 16px;
      margin-top: 20px;
      b {
        font-weight: 500; } }
    &-btn {
      display: none; }
    &-number {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 59px;
      height: 59px;
      background: #EEFAFF;
      border: 1px solid #33C6F8;
      border-radius: 6px;
      margin: 0 auto;
      font-weight: bold;
      font-size: 20px;
      line-height: 24px;
      color: #33C6F8;
      font-family: 'Gilroy', sans-serif;
      &--active {
        color: #fff;
        background: linear-gradient(282.59deg, #64D8FF 20.95%, #33C6F8 75.63%);
        border: none; } } }
  &__welcome {
    font-size: 32px;
    text-align: center;
    margin-top: 25px;
    &--mobile {
      display: none; } } }

@media(max-width: 900px) {
  .stages {
    &__title {
      text-align: left;
      font-size: 24px; }
    &__text {
      text-align: left;
      max-width: 672px;
      font-size: 14px;
      margin: 0;
      margin-top: 20px; }
    &__list {
      margin-top: 25px; }
    &__item {
      justify-content: flex-start;
      padding-bottom: 40px;
      &:last-child {
        padding-bottom: 0; }
      &::before {
        left: 22px; }
      &-block-1 {
        order: 1;
        width: auto;
        margin-left: 20px;
        justify-content: flex-start; }
      &-block-2 {
        width: auto; }
      &-block-3 {
        display: none; }
      &-title {
        font-size: 16px;
        margin-top: 11px; }
      &-text {
        font-size: 12px;
        margin-top: 10px;
        max-width: 588px; }
      &-number {
        width: 45px;
        height: 45px;
        margin: 0; }
      & .button {
        display: none; }
      &-btn {
        text-decoration: none;
        display: block;
        font-weight: 500;
        font-size: 12px;
        line-height: 130.29%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #26B58B;
        padding-right: 40px;
        margin-top: 10px; } }
    &__welcome {
      display: none;
      &--mobile {
        display: block;
        margin-left: 20px; } } } }
