body {
	min-width: 320px;
	margin: 0;
	padding: 0;
	font-family: 'Gotham-Pro', sans-serif; }

input,
button {
	font-family: inherit;
	outline: none;
	transition: .2s; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none; }

ul {
	list-style: none;
	margin: 0;
	padding: 0; }

p {
	margin-block-start: 0px;
	margin-block-end: 0px;
	line-height: 145%;
	color: #252829; }

h1,h2,h3,h4,h5 {
	font-family: 'Gilroy', sans-serif;
	line-height: 120%;
	color: #252829;
	margin: 0; }

*, *:before, *:after {
	box-sizing: border-box; }

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden; }
