.footer {
  padding: 0 15px;
  background: #EEFAFF;
  &__wrap {
    max-width: 1715px;
    width: 100%;
    margin: 0 auto;
    padding: 30px 0; }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #B5E3F2;
    padding-bottom: 30px; }
  &__logo {
    width: 183px; }
  & .nav__list {
    margin: auto; }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 25px; }
  &__link {
    text-decoration: none;
    font-weight: 300;
    font-size: 16px;
    line-height: 130.29%;
    color: #252829;
    transition: .2s;
    &:hover,
    &:focus {
      color: #33C6F8; } }
  &__list {
    display: flex;
    align-items: center; }
  &__item {
    margin-right: 30px;
    transition: .2s;
    &:last-child {
      margin: 0; }
    &:hover,
    &:focus {
      transform: scale(1.1); }
    &:active {
      transform: scale(1); } } }


@media(max-width: 700px) {
  .footer {
    &__top {
      flex-direction: column;
      border: none;
      padding: 0; }
    &__logo {
      width: 138px;
      margin-bottom: 20px; }
    &__bottom {
      flex-direction: column;
      margin-top: 20px; }
    &__link {
      order: 1;
      font-size: 14px;
      margin-top: 17px; } } }
