.questions {
  margin-top: -320px;
  &__wrap {
    background: #FFFFFF;
    box-shadow: 9px 13px 33px rgba(33, 134, 169, 0.1);
    border-radius: 10px;
    padding: 80px 60px; }
  &__title {
    font-size: 32px; }
  &__list {
    margin-top: 30px; }
  &__item {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0; }
    &-label {
      display: flex;
      align-items: center;
      padding: 20px;
      padding-right: 70px;
      background: #EEFAFF;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        top: calc(50% - 3px);
        right: 40px;
        width: 11px;
        height: 11px;
        border: 1px solid #26B58B;
        border-width: 0 2px 2px 0;
        transform: rotate(-135deg); } }
    &-number {
      font-family: Gilroy, sans-serif;
      font-weight: bold;
      font-size: 18px;
      line-height: 22px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: #FFFFFF;
      background: linear-gradient(282.59deg, #64D8FF 20.95%, #33C6F8 75.63%);
      border-radius: 6px;
      min-width: 43px;
      height: 43px; }
    &-title {
      margin-left: 20px;
      font-weight: 600;
      font-size: 18px;
      max-width: 700px; }
    &-wrap {
      display: none;
      background: #EEFAFF;
      border-radius: 6px;
      margin-top: -5px;
      padding: 0 35px 25px 80px; }
    &-text {
      font-weight: 300;
      font-size: 16px;
      &--marg {
        margin-top: 20px;
        margin-left: 30px; }
      b {
        font-weight: 500; } } } }

.questions__input:checked ~ .questions__item-wrap {
  display: block; }
.questions__input:checked ~ .questions__item-label::before {
  transform: rotate(45deg); }

@media(max-width: 1000px) {
  .questions {
    margin-top: -60px; } }

@media(max-width: 900px) {
  .questions {
    &__wrap {
      background: transparent;
      box-shadow: none;
      padding: 0; }
    &__title {
      font-size: 24px; }
    &__item {
      margin-bottom: 10px;
      &-label {
        padding: 15px;
        align-items: flex-start;
        &::before {
          display: none; } }
      &-number {
        min-width: 30px;
        height: 30px;
        font-size: 14px; }
      &-title {
        font-size: 14px;
        margin-left: 10px; }
      &-wrap {
        padding: 0 15px;
        padding-bottom: 15px; }
      &-text {
        font-size: 14px; } } } }
